import React from 'react'

const Page = () => {
  return (
    <div className=" flex justify-center mt-[150px] ">
    <div className='max-w-[550px] w-full rounded-[25px] border-[2px] border-borderColor flex flex-col gap-4 p-6'>
        <h2 className="text-3xl font-bold">Login</h2>
        <div className=" flex flex-col gap-2">
        <div className="flex flex-col gap-1 ">
        <label className='text-[18px]'>Username</label>
        <input placeholder='Type in your username...' type="text" className=' ml-2 input h-[40px] px-2' />
        </div>
        <div className="flex flex-col gap-1 ">
        <label className='text-[18px]'>Password</label>
        <input placeholder='Type in your password...' type="password" className=' ml-2 input h-[40px] px-2' />
        </div>
       
        </div>
        <div className="flex items-center justify-between">
            Link
        </div>
    </div>
    </div>
  )}
export default Page;